// UserProfile.jsx (s dng fetch)
import React, { useEffect, useState } from 'react';
import "./UserProfile.scss";
const UserProfile = () => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetch('/api/user/profile')
      .then((res) => {
        if (!res.ok) {
          throw new Error('Khng th ly thng tin ngi dng');
        }
        return res.json();
      })
      .then((data) => {
        setUser(data);
        setLoading(false);
      })
      .catch((err) => {
        console.error('Li khi fetch profile:', err);
        setLoading(false);
      });
  }, []);

  if (loading) {
    return <div>ang ti d liu n, ng hi na b </div>;
  }

  if (!user) {
    return <div>Khng tm thy thng tin ngi dng nha </div>;
  }

  return (
    <div className="profile-container">
      <h2>Thng tin ca {user.name}</h2>
      <p><strong>Email:</strong> {user.email}</p>
      <p><strong>ST:</strong> {user.phone || 'Khng c'}</p>
      <p><strong>a ch:</strong> {user.address || 'Cha cp nht'}</p>
    </div>
  );
};

export default UserProfile;